import { getCookie } from "./utilities";

const BURGER_API_URL = 'https://norma.nomoreparties.space/api';
const HEADERS = {
    'Content-Type': 'application/json',
    Authorization: 'Bearer ' + getCookie('token')
}

const checkResponse = (res) => {
    return res.ok ? res.json() : res.json().then((err) => Promise.reject(err));
};

const request = (url, options) => {
    return fetch(url, options)
        .then(checkResponse)
}

export const getData = () => {
    return request(`${BURGER_API_URL}/ingredients`, {
        headers: HEADERS
    })
};

export const sendPostOrderToServer = (ingredients) => {
    return request(`${BURGER_API_URL}/orders`,
        {
            method: 'POST',
            headers: HEADERS,
            body: JSON.stringify({
                "ingredients": ingredients

            })
        })
};

export const getRegister = (form) => {
    return request(`${BURGER_API_URL}/auth/register`, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-Type': 'application/json',
            // Authorization: 'Bearer ' + getCookie('token')
        },
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: JSON.stringify(form)
    });
};

export const getUserRequest = () => {
    return request(`${BURGER_API_URL}/auth/login`,
        {
            method: 'GET',
            mode: 'cors',
            cache: 'no-cache',
            credentials: 'same-origin',
            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + getCookie('token')
            },
            redirect: 'follow',
            referrerPolicy: 'no-referrer'
        });
}

export const loginRequest = (form) => {
    return request(`${BURGER_API_URL}/auth/login`, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-Type': 'application/json'
        },
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: JSON.stringify(form)
    });
};

export const logoutRequest = (refreshToken) => {
    return request(`${BURGER_API_URL}/auth/logout`, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-Type': 'application/json'
        },
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: JSON.stringify(`token: ${refreshToken}`)
    });
};