import React, { useCallback } from 'react';
import style from './login.module.css';
import AppHeader from '../../appHeader/AppHeader';
import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, Navigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { signIn } from '../../../services/actions/profile';
import { token } from '../../../utils/constants';

export const Login = () => {
    const dispatch = useDispatch();
    const profileData = useSelector((state) => state.profile);

    const [form, setValue] = React.useState({ email: '', password: '' });
    const [state, setState] = React.useState(true);
    const inputRef = React.useRef(null)
    const onIconClick = () => {
        setTimeout(() => inputRef.current.focus(), 0);
        setState(!state);
    }

    const onChange = e => {
        setValue({ ...form, [e.target.name]: e.target.value });
    };

    let login = useCallback(
        e => {
            e.preventDefault();
            dispatch(signIn(form));
        },
        [signIn, form]
    );

    if (profileData.user || token) {
        return (
          <Navigate
            to={'/'}
          />
        );
      }

    return (
        <>
            <AppHeader />
            <div className={style.login}>
                <form className={''} >
                    <p className={'text text_type_main-medium'}></p>
                    <Input
                        type={'text'}
                        placeholder={'E-mail'}
                        onChange={onChange}
                        value={form.email}
                        name={'email'}
                        error={false}
                        ref={inputRef}
                        errorText={''}
                        size={'default'}
                        extraClass="ml-1 mt-6"
                    />
                    <Input
                        type={state ? 'password' : 'text'}
                        placeholder={''}
                        onChange={onChange}
                        icon={state ? 'HideIcon' : 'ShowIcon'}
                        value={form.password}
                        name={'password'}
                        error={false}
                        ref={inputRef}
                        onIconClick={onIconClick}
                        errorText={''}
                        size={'default'}
                        extraClass="ml-1 mt-6"
                    />
                    <Button htmlType="button" type="primary" size="large" extraClass={'mt-6'} onClick={login} >
                        
                    </Button>
                    <p className={'text text_type_main-default mt-20'} >  ? <Link to="/register" className={style.loginLink}>  </Link> </p>
                    <p className={'text text_type_main-default mt-4'} > ? <Link to="/forgotPassword" className={style.loginLink}>   </Link> </p>
                </form>
            </div>
        </>
    )
}