import { GET_REGISTER_REQUEST, GET_REGISTER_SUCCESS, GET_REQUEST_FAILED } from "../actions/profile"

const initialState = {
    user: null,
    isLoading: false,
    hasError: false
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_REGISTER_REQUEST: {
            return {
                ...state,
                isLoading: true
            }
        }
        case GET_REGISTER_SUCCESS: {
            return {
                ...state,
                isLoading: false,
                hasError: false,
                user: action.user
            }
        }
        case GET_REQUEST_FAILED: {
            return {
                ...state,
                isLoading: false,
                hasError: true
            }
        }        
        default:
            return state;
    }
} 
